$(document).ready(function () {
  attachCheckboxEvents();

  var levelingData = {};
  $.getJSON('/db/misc/leveling.json', function(data){
    levelingData = data;
  })
  .fail(function() {
    alert("error");
  });

  function getLevelUpCostDataForStars(stars) {
    var levelUpStarsCost = levelingData["LevelUpCosts"];
    for (var i in levelUpStarsCost) {
      var thisLevelUpStarsCost = levelUpStarsCost[i];
      if (thisLevelUpStarsCost.Stars == stars) {
        return thisLevelUpStarsCost.LevelsCost;
      }
    }
  }

  function getEvoTokensDataForColor(color) {
    var evoTokens = levelingData["EvoTokens"];
    for (var i in evoTokens) {
      var thisEvoTokens = evoTokens[i];
      if (thisEvoTokens.Color == color) {
        return thisEvoTokens.Items;
      }
    }
  }

  function calculateEvoTokens(color, from, to) {
    var allTokens = {};
    var evoTokensData = getEvoTokensDataForColor(color);
    if (to <= evoTokensData.length && from < to) {
      for (var i = from; i < to; i++) {
        evoTokensData[i].Tokens.forEach(function(token){
          if (allTokens[token.ID]) {
            allTokens[token.ID] = allTokens[token.ID] + token.Amount;
          } else {
            allTokens[token.ID] = token.Amount;
          }
        });
      }
    }

    return allTokens;
  }

  function calculateLevelUpCost(stars, from, to) {
    var levelUpData = getLevelUpCostDataForStars(stars);
    var cost = 0;
    if (to <= levelUpData.length && from < to) {
      for (var i = from; i < to; i++) {
        cost = cost + levelUpData[i];
      }
    }
    return cost;
  }

  function calculateMoveUpgradeTPCost(from, to) {
    var moveUpgradeData = levelingData["MoveUpgrade"];
    var cost = 0;
    if (to <= moveUpgradeData.length && from < to) {
      for (var i = from; i < to; i++) {
        cost = cost + parseInt(moveUpgradeData[i].TP);
      }
    }
    return cost;
  }

  function updateLevelUpResultsTable(stars, amount) {
    if (stars <= 0) {
      $('#levelUpResultsTotal').text(amount);
    } else {
      $('#levelUpResults'+stars+'Star').text(amount);
    }
  }

  function updateMoveUpgradeResultsTable(moveUpgradeCost) {
    for (var key in moveUpgradeCost) {
      $('#moveUpgradeResults' + key).text(moveUpgradeCost[key]);
    }
  }

  function updateEvoTokenResultsTable(evoTokensCost) {
    for (var key in evoTokensCost) {
      $('#' + key).text(evoTokensCost[key]);
    }
  }

  function resetEvoTokenResultsTable() {
    $('#evoTokenResultsTable .token-cost-result-evolution').text(0);
  }

  function attachCheckboxEvents() {
    $('#levelUpCostForm :checkbox, #moveUpgradeForm :checkbox').change(function(e) {
      if ($(this).is(':checked')) {
        $('input[name="start-' + e.target.name + '"]').prop("disabled", false);
        $('input[name="target-' + e.target.name + '"]').prop("disabled", false);
      } else {
        $('input[name="start-' + e.target.name + '"]').prop("disabled", true);
        $('input[name="target-' + e.target.name + '"]').prop("disabled", true);
      }
    });
  }


  $('#levelUpCostForm').on('submit', function(e) {
    e.preventDefault();

    var data = $(this).serializeArray().reduce(function(obj, item) {
      obj[item.name] = item.value;
      return obj;
    }, {});

    if (data) {
      var totalCost = 0;
      for (var i = 1; i < 5; i++) {
        if (data['level-up-'+i+'-star'] && data['level-up-'+i+'-star'].toLowerCase() == 'on') {
          var startLevel = data['start-level-up-'+i+'-star'] ? parseInt(data['start-level-up-'+i+'-star']) : 0;
          var targetLevel = data['target-level-up-'+i+'-star'] ? parseInt(data['target-level-up-'+i+'-star']) : 0;
          var cost = calculateLevelUpCost(i, startLevel, targetLevel);
          totalCost = totalCost + cost;
          updateLevelUpResultsTable(i, cost);
        } else {
          updateLevelUpResultsTable(i, 0);
        }
      }
      updateLevelUpResultsTable(0, totalCost);
    }
  });

  $('#moveUpgradeForm').on('submit', function(e) {
    e.preventDefault();

    var moveUpgradeCost = {
      "Black": 0,
      "Yellow": 0,
      "Green": 0,
      "Purple": 0,
      "Blue": 0,
      "Red": 0,
    };

    var data = $(this).serializeArray().reduce(function(obj, item) {
      obj[item.name] = item.value;
      return obj;
    }, {});

    if (data) {
      for (var i = 1; i < 7; i++) {
        if (data['move-upgrade-'+i] && data['move-upgrade-'+i].toLowerCase() == 'on') {
          var startLevel = data['start-move-upgrade-'+i] ? parseInt(data['start-move-upgrade-'+i]) : 0;
          var targetLevel = data['target-move-upgrade-'+i] ? parseInt(data['target-move-upgrade-'+i]) : 0;
          var moveType = data['type-move-upgrade-'+i] ? data['type-move-upgrade-'+i] : '';
          var cost = calculateMoveUpgradeTPCost(startLevel, targetLevel);
          moveUpgradeCost[moveType] = moveUpgradeCost[moveType] + cost;
        }
      }
    }

    updateMoveUpgradeResultsTable(moveUpgradeCost);
  });

  function getEvolutionLevel(stars, tier) {
    var tierLevel = 0;
    var starsLevel = 0;
    switch (tier.toLowerCase()) {
      case 'bronze':
        tierLevel = 0;
        break;
      case 'silver':
        tierLevel = 1;
        break;
      case 'gold':
        tierLevel = 2;
        break;
    }
    switch (stars) {
      case 1:
        starsLevel = 0;
        break;
      case 2:
        starsLevel = 3;
        break;
      case 3:
        starsLevel = 6;
        break;
      case 4:
        starsLevel = 9;
        break;
    }
    return (tierLevel + starsLevel);
  }

  $('#evolutionForm').on('submit', function(e) {
    e.preventDefault();
    resetEvoTokenResultsTable();

    var data = $(this).serializeArray().reduce(function(obj, item) {
      obj[item.name] = item.value;
      return obj;
    }, {});

    if (data) {
      var fromStars = data["start-stars-evolution"] ? parseInt(data["start-stars-evolution"]) : 0;
      var fromTier = data["start-tier-evolution"] ? data["start-tier-evolution"] : '';
      var toStars = data["target-stars-evolution"] ? parseInt(data["target-stars-evolution"]) : 0;
      var toTier = data["target-tier-evolution"] ? data["target-tier-evolution"] : '';
      var wrestlerColor = data["wrestler-color-evolution"] ? data["wrestler-color-evolution"] : '';
      var evoFromLevel = getEvolutionLevel(fromStars, fromTier);
      var evoToLevel = getEvolutionLevel(toStars, toTier);
      if (evoToLevel > evoFromLevel) {
        var tokensCost = calculateEvoTokens(wrestlerColor, evoFromLevel, evoToLevel);
        updateEvoTokenResultsTable(tokensCost);
      }
    }
  });

});



